import React from 'react';
import Popup from 'reactjs-popup';
import MenuItem from './MenuItem';
import DarkModeToggle from './DarkModeToggle';
import { ReactSVG } from 'react-svg';
import chippies from '../../assets/svg/chippies.svg';
import home from '../../assets/svg/home-light.svg';
import images from '../../assets/svg/images.svg';
import contact from '../../assets/svg/contact.svg';
import closeBtn from '../../assets/svg/close.svg';
import Contacts from '../homePage/Contacts';
import BodyPopup from '../Utils/BodyPopup';
function Header() {
   return (
      <header className={`header`}>
         <div className="container">
            <div className="header__body">
               <div className="header__top">
                  <div className="header__logo">
                     <ReactSVG className="header__svg" src={chippies} />
                  </div>
                  <nav className="menu">
                     <ul className="menu__list">
                        <MenuItem path="/">Home</MenuItem>
                        <MenuItem path="/works">Works</MenuItem>
                        <Popup
                           trigger={
                              <li className="menu__item">
                                 <button className="menu__btn">Contacts</button>
                              </li>
                           }
                           modal
                           nested
                        >
                           {(close) => (
                              <div className="menu-modal modal">
                                 <button
                                    className="menu-modal__close close"
                                    onClick={close}
                                 >
                                    <ReactSVG
                                       className="header__svg"
                                       src={closeBtn}
                                    />
                                 </button>
                                 <div className="menu-modal__content content">
                                    <BodyPopup />
                                 </div>
                              </div>
                           )}
                        </Popup>
                     </ul>
                     <DarkModeToggle />
                  </nav>
               </div>
               <ul className="header__bottom-wrapper">
                  <div className="header__bottom">
                     <MenuItem path="/">
                        <ReactSVG className="header__svg" src={home} />
                        <p>Home</p>
                     </MenuItem>
                     <MenuItem path="/works">
                        <ReactSVG className="header__svg" src={images} />
                        <p>Works</p>
                     </MenuItem>
                     <Popup
                        trigger={
                           <li className="menu__item">
                              <ReactSVG className="header__svg" src={contact} />
                              <p>Contacts</p>
                           </li>
                        }
                        modal
                        nested
                     >
                        {(close) => (
                           <div className="menu-modal modal">
                              <button
                                 className="menu-modal__close close"
                                 onClick={close}
                              >
                                 <ReactSVG
                                    className="header__svg"
                                    src={closeBtn}
                                 />
                              </button>
                              <div className="menu-modal__content content">
                                 <BodyPopup />
                              </div>
                           </div>
                        )}
                     </Popup>
                  </div>
               </ul>
            </div>
         </div>
      </header>
   );
}

export default Header;
