import React from 'react';
import Title from '../../Utils/Title';
import Section from '../../Utils/Section';
import posters from '../../../data/images';
import WorksItem from './WorksItem';

function WorksHomePage() {
   return (
      <Section class="works-home-page" side="">
         <Title class="works-home-page__title">Some Works</Title>
         <div className="works-home-page__grid">
            <WorksItem el={posters[0]} key='0' />
            <WorksItem el={posters[1]} key='1' />
            <WorksItem el={posters[2]} key='2' />
         </div>
      </Section>
   );
}

export default WorksHomePage;
