import Title from '../Utils/Title';
import Section from '../Utils/Section';
import List from '../Utils/List';
function Skills() {
   return (
      <Section class="skills" side="right">
         <Title>Skills</Title>
         <List class="skills__list">
            <li className="skills__item">Photoshop</li>
            <li className="skills__item">Illustrator</li>
            <li className="skills__item">InDesign</li>
            <li className="skills__item">Premiere Pro</li>
            <li className="skills__item">After Effects</li>
            <li className="skills__item">Affinity</li>
         </List>
      </Section>
   );
}
export default Skills;
