import Title from '../Utils/Title';
import Text from '../Utils/Text';
import Section from '../Utils/Section';
function Purpose() {
   return (
      <Section class="purpose" side="left">
         <Text class="purpose__text">
            I want to "use graphic design to sell things, make things look
            better, make people laugh, make people cry, and (every once in a
            while) change the world."
         </Text>
         <Title class="purpose__title">Purpose</Title>
      </Section>
   );
}
export default Purpose;
