import React from 'react';
import Title from '../../Utils/Title';
import Section from '../../Utils/Section';
import certificates from '../../../data/certificatesImgs';
import WorksItem from '../Works/WorksItem';

function Certificates() {
   return (
      <Section class="works-home-page" side="">
         <Title class="works-home-page__title">Certificates</Title>
         <div className="works-home-page__grid">
            <WorksItem el={certificates[0]} key="0" />
            <WorksItem el={certificates[1]} key="1" />
            <WorksItem el={certificates[2]} key="1" />
         </div>
      </Section>
   );
}

export default Certificates;
