import React from 'react';
import Title from '../components/Utils/Title';
import Section from '../components/Utils/Section';
import posters from '../data/images';
import WorksItem from '../components/homePage/Works/WorksItem';
import Contacts from '../components/homePage/Contacts';

function Works() {
   return (
      <>
         <Section class="works-home-page" side="">
            <Title class="works-home-page__title">Works</Title>
            <div className="works-home-page__grid">
               {posters.map((el, index) => (
                  <WorksItem el={el} key={index} />
               ))}
            </div>
         </Section>
         <Contacts />
      </>
   );
}

export default Works;
