import Title from '../Utils/Title';
import Section from '../Utils/Section';
import List from '../Utils/List';
function Statistics() {
   return (
      <Section class="statistics" side="left">
         <List class="statistics__list">
            <li className="statistics__item">Clients 30 </li>
            <li className="statistics__item">Posters 10 </li>
            <li className="statistics__item">Logos 2</li>
            <li className="statistics__item">Videos 10</li>
         </List>
         <Title>Statistics</Title>
      </Section>
   );
}
export default Statistics;
