import cipi from '../../assets/img/cipi.jpg';
import Title from '../Utils/Title';
import Text from '../Utils/Text';
import Section from '../Utils/Section';
function Intro() {
   return (
      <Section class="intro" side="left">
         <div className="intro__info">
            <Title class="intro__title">
               Hi, my name's Ciprian and I'm a professional graphic designer.
            </Title>
            <Text class="intro__text">
               Through my projects I can make your business expand and grow. I
               want to make people laugh, cry, feel pleasant emotions and
               remember pleasant moments.
            </Text>
         </div>
         <div className="intro__img">
            <img src={cipi} alt="Marandici Ciprian" />
         </div>
      </Section>
   );
}
export default Intro;
