import React, { useState, useEffect } from 'react';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';

function WorksItem({ el }) {
   const [isModalOpen, setIsModalOpen] = useState(false);

   const handleModalOpen = () => {
      setIsModalOpen(true);
      window.history.pushState({ page: 'modal' }, 'Modal Page', '?img=modal');
   };

   const handleModalClose = (e) => {
      e.stopPropagation();
      setIsModalOpen(false);
      window.history.replaceState(
         null,
         document.title,
         window.location.pathname
      );
   };

   const handlePopstate = () => {
      setIsModalOpen(false);
   };

   useEffect(() => {
      const body = document.querySelector('body');
      if (body) {
         body.style.overflow = isModalOpen ? 'hidden' : 'auto';

         // Check if it's not a mobile device
         if (!/Mobi|Android/i.test(navigator.userAgent)) {
            body.style.paddingRight = isModalOpen ? '16px' : '0px';
         }
      }

      window.addEventListener('popstate', handlePopstate);

      return () => {
         window.removeEventListener('popstate', handlePopstate);

         if (body) {
            body.style.overflow = 'auto';
            body.style.paddingRight = '0px';
         }
      };
   }, [isModalOpen]);

   return (
      <div className="works-item" onClick={handleModalOpen}>
         <LazyLoadImage
            className="works-item__img"
            src={el.img}
            alt={el.name}
            effect="blur"
            placeholder={
               <img
                  src={el.imgMin}
                  alt="Placeholder"
                  className="works-item__placeholder"
               />
            }
         />
         <svg
            xmlns="http://www.w3.org/2000/svg"
            width="20"
            height="20"
            viewBox="0 0 20 20"
            className="works-item__open-icon"
         >
            <g fill="currentColor">
               <path d="M14.5 14.5v-3.25a.5.5 0 0 1 1 0V15a.5.5 0 0 1-.5.5H5a.5.5 0 0 1-.5-.5V5a.5.5 0 0 1 .5-.5h3.75a.5.5 0 0 1 0 1H5.5v9z" />
               <path d="M10.354 10.354a.5.5 0 0 1-.708-.708l5-5a.5.5 0 0 1 .708.708z" />
               <path d="M15.5 8.5a.5.5 0 0 1-1 0V5a.5.5 0 0 1 1 0z" />
               <path d="M11.5 5.5a.5.5 0 0 1 0-1H15a.5.5 0 0 1 0 1z" />
            </g>
         </svg>
         <h2 className="works-item__title text">{el.name}</h2>
         {isModalOpen && (
            <div className="modal" onClick={handleModalClose}>
               <span className="modal__close">
                  <svg
                     xmlns="http://www.w3.org/2000/svg"
                     width="40"
                     height="40"
                     viewBox="0 0 24 24"
                  >
                     <path
                        fill="none"
                        stroke="currentColor"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2.5"
                        d="m7 7l10 10M7 17L17 7"
                     />
                  </svg>
               </span>
               <div className="modal__content">
                  <img
                     className="modal__img"
                     src={el.img}
                     alt={el.name}
                     onClick={(e) => e.stopPropagation()}
                  />
               </div>
            </div>
         )}
      </div>
   );
}

export default WorksItem;
